import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import {
  Container, Header, Button, Form, Divider, Table, Label, Segment,
} from 'semantic-ui-react';
import Number from 'react-number-format';
import { Contract, utils, BigNumber } from 'ethers';
import moment from 'moment';
import BN from 'bignumber.js';
import { toast } from 'react-toastify';
import copy from 'copy-to-clipboard';
import Page from '../components/Page';
import LinkExternal from '../components/Link/LinkExternal';
import EtherscanLink from '../components/Link/EtherscanLink';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';

import { useSelector, useDispatch } from 'react-redux';
import * as S from '../store/selectors';
import * as A from '../store/actions';

import {
  FancyNftHeroesS1Address,
  FancyNftStakingAddress,
  FancyTokenAddress,
  FancyTokenSupplierAddress,
  nullAddress,
} from '../constants';
import nftHeroesAbi from '../abis/FancyNftHeroesS1Abi.json';
import FancyNftStakingAbi from '../abis/FancyNftStakingAbi.json';
import FancyTokenAbi from '../abis/FancyTokenAbi.json';
import FancyTokenSupplierAbi from '../abis/FancyTokenSupplierAbi.json';



function NftStakingPage() {
  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const getNftContract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyNftHeroesS1Address, nftHeroesAbi, provider);
  }
  const getFarmContract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyNftStakingAddress, FancyNftStakingAbi, provider);
  }
  const getTokenContract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyTokenAddress, FancyTokenAbi, provider);
  }
  const getTokenSupplierContract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(FancyTokenSupplierAddress, FancyTokenSupplierAbi, provider);
  }

  const [supplierStartTime, setSupplierStartTime] = useState(0);
  const [farmLastSupply, setFarmLastSupply] = useState('0');
  const [farmRrewardRate, setFarmRrewardRate] = useState(BigNumber.from(0));

  // address
  const [owner, setOwner] = useState(nullAddress);
  const [farmStakedNft, setFarmStakedNft] = useState('0');

  // mints
  const [balanceNft, setBalanceNft] = useState('0');
  const [balanceToken, setBalanceToken] = useState('0');
  const [stakedNft, setStakedNft] = useState('0');
  const [farmRate, setFarmRate] = useState('0');

  const [stakerInfo, setStakerInfo] = useState({
    amount: BigNumber.from(0),
    initialRewardRate: BigNumber.from(0),
    reward: BigNumber.from(0),
    claimedReward: BigNumber.from(0),
  });

  const [farmState, setFarmState] = useState({
    totalStaked: BigNumber.from(0),
    historicalRewardRate: BigNumber.from(0),
  });
  const [unclaimedRewards, setUnclaimedRewards] = useState('0');
  let farmShare = '0';
  if (stakerInfo.amount.gt('0') && farmState.totalStaked.gt('0')) {
    farmShare = utils.formatUnits(
      stakerInfo.amount.mul('1000000').div(farmState.totalStaked),
      4
    );
  }

  useEffect(() => {
    let farmRate = '0';
    if (stakerInfo.amount.gt(0) && farmRrewardRate.gt(0)) {
      farmRate = farmRrewardRate
        .mul(24 * 60 * 60)
        .mul(stakerInfo.amount)
        .div(farmState.totalStaked);
    }
    setFarmRate(utils.formatEther(farmRate.toString()));
  }, [farmRrewardRate, farmState, stakerInfo]);

  const getCurrentTokenSupply = (deltaTime, farmRrewardRate) => {
    let tokenSupply = BigNumber.from(0);
    if (deltaTime > 0) {
      tokenSupply = farmRrewardRate.mul(deltaTime);
    }
    return tokenSupply;
  }

  // update
  const [initDone, setInitDone] = useState(false);
  const [updating, setUpdating] = useState(false);
  const update = async () => {
    if (updating) return;
    setUpdating(true);
    try {
      const contractNft = getNftContract();
      const contractToken = getTokenContract();
      const contractFarming = getFarmContract();
      const contractTokenSupplier = getTokenSupplierContract();

      const supplierStartTime = await contractTokenSupplier.startTime();
      setSupplierStartTime(supplierStartTime);
      const farmRrewardRate = await contractTokenSupplier.rewardRateOf(FancyNftStakingAddress);
      setFarmRrewardRate(farmRrewardRate);
      let farmLastSupply = await contractTokenSupplier.lastSupply(FancyNftStakingAddress);
      farmLastSupply = farmLastSupply.toNumber();
      setFarmLastSupply(farmLastSupply);


      const farmState = await contractFarming.state();
      setFarmState(farmState);
      const stakerInfo = await contractFarming.stakers(account);
      setStakerInfo(stakerInfo);

      const timestamp = Math.floor(Date.now() / 1000);
      let deltaTime = 0;
      if (timestamp > farmLastSupply)
        deltaTime = timestamp - farmLastSupply;

      const currentTokenSupply = getCurrentTokenSupply(
        deltaTime, farmRrewardRate
      );
      const additionalRewardRate = currentTokenSupply
        .shl(40)
        .div(farmState.totalStaked);
      const hrr = farmState.historicalRewardRate
        .add(additionalRewardRate);
      const unrewarded = hrr.sub(stakerInfo.initialRewardRate)
        .mul(stakerInfo.amount)
        .shr(40);
      const unclaimed = stakerInfo.reward
        .add(unrewarded)
        .sub(stakerInfo.claimedReward);
      setUnclaimedRewards(utils.formatEther(unclaimed));



      // address
      const owner = await contractFarming.owner();
      setOwner(owner);

      const farmStakedNft = await contractNft.balanceOf(FancyNftStakingAddress);
      setFarmStakedNft(parseInt(farmStakedNft.toString()));

      if (account) {
        // mints
        const balanceToken = await contractToken.balanceOf(account);
        setBalanceToken(utils.formatEther(balanceToken));

        const balanceNft = await contractNft.balanceOf(account);
        setBalanceNft(parseInt(balanceNft.toString()));

        const stakedNft = await contractFarming.balanceOf(account);
        setStakedNft(parseInt(stakedNft.toString()));


      } else {
        setBalanceToken('0');
        setBalanceNft('0');
        setStakedNft('0');
      }

    } catch (error) { console.error(error); }
    setInitDone(true);
    setUpdating(false);
  }

  const [blockNumber, setBlockNumber] = useState(-1);
  const [time, setTime] = useState(0);
  useEffect(() => {
    if (library) {
      library.on('block', (blockNumber) => {
        setBlockNumber(blockNumber);
        update();
      });
    }

    setTime(Date.now());
    const intervalId = setInterval(() => {
      setTime(Date.now());
    }, 1 * 1000);

    return () => {
      clearInterval(intervalId);
    };
  }, []);
  useEffect(() => {
    if (library) {
      library.on('block', (blockNumber) => {
        setBlockNumber(blockNumber);
        update();
      });
    }
  }, [library]);

  useEffect(() => {
    if (stakerInfo.amount.eq(0)) return;
    try {
      const timestamp = Math.floor(Date.now() / 1000);
      let deltaTime = 0;
      if (timestamp > farmLastSupply)
        deltaTime = timestamp - farmLastSupply;

      const currentTokenSupply = getCurrentTokenSupply(
        deltaTime, farmRrewardRate
      );
      const additionalRewardRate = currentTokenSupply
        .shl(40)
        .div(farmState.totalStaked);
      const hrr = farmState.historicalRewardRate
        .add(additionalRewardRate);
      const unrewarded = hrr.sub(stakerInfo.initialRewardRate)
        .mul(stakerInfo.amount)
        .shr(40);
      const unclaimed = stakerInfo.reward
        .add(unrewarded)
        .sub(stakerInfo.claimedReward);
      setUnclaimedRewards(utils.formatEther(unclaimed));
    } catch (error) { }
  }, [time]);
  // update end


  const doTx = async (promiseTx) => {
    try {
      const tx = await promiseTx;
      if (!tx) return;
      toast.info('Pending tx doTx', tx.hash);
      await tx.wait(1);
      toast.success('Done tx doTx', tx.hash);
    } catch (error) {
      console.error(error);
      toast.error('Error doTx');
    }
  }


  const claimRewards = async () => {
    const contractFarming = getFarmContract(true);
    await doTx(contractFarming.claimReward());
  }

  const stakeAll = async () => {
    const contractNft = getNftContract();
    let balance = await contractNft.balanceOf(account);
    if (balance > 200) balance = 200;
    let tokenIds = [];
    for (let i = 0; i < balance; i++) {
      let tokenId = await contractNft.tokenOfOwnerByIndex(account, i);
      tokenIds.push(tokenId);
    }
    const contractFarming = getFarmContract(true);
    await doTx(contractFarming.stake(tokenIds));
    await update();
  }

  const unstakeAll = async () => {
    const contractFarming = getFarmContract(true);
    let balance = stakedNft;
    if (balance > 200) balance = 200;
    let tokenIds = [];
    for (let i = 0; i < balance; i++) {
      let tokenId = await contractFarming.tokenOfOwnerByIndex(account, i);
      tokenIds.push(tokenId);
    }
    await doTx(contractFarming.unstake(tokenIds));
    await update();
  }

  return (
    <Page title={'NFT Staking'}>
      <Container>
        <br />
        <Segment>
          <Header as='h1'>
            <Header.Content>
              NFT Staking
              <Header.Subheader>
                <div>Smart Contract: <EtherscanLink address={FancyNftStakingAddress}>{FancyNftStakingAddress}</EtherscanLink></div>
                <div>Owner: <EtherscanLink address={owner}>{owner}</EtherscanLink></div>
                <div>Last Block: <Number value={blockNumber}
                  displayType='text' thousandSeparator />
                </div>
              </Header.Subheader>
            </Header.Content>
          </Header>
        </Segment>


        <Segment>
          <div>Address: <EtherscanLink address={account}>{account}</EtherscanLink></div>
          <div>Balance NFT: <EtherscanLink token={`${FancyNftHeroesS1Address}?a=${account}`}><Number value={balanceNft} displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></EtherscanLink></div>
          <div>Balance TOKEN: <EtherscanLink token={`${FancyTokenAddress}?a=${account}`}><Number value={balanceToken} displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></EtherscanLink></div>

          <Segment>
            <div>Total Farm Staked NFT: <Number value={farmStakedNft} displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></div>
            <div>Total Farm Staked NFT Rate: <Number value={utils.formatEther(farmState.totalStaked)} displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></div>
            <div>Farm Reward Rate/1day: <Number value={utils.formatEther(farmRrewardRate.mul(24 * 60 * 60))} suffix=' TOKEN' displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></div>
          </Segment>

          <Segment>
            <div>Staked NFT: <Number value={stakedNft} displayType='text' thousandSeparator /></div>
            <div>Staked NFT Rate: <Number value={utils.formatEther(stakerInfo.amount)} displayType='text' thousandSeparator /></div>
            <div>My Farm Reward Rate/1day: <Number value={farmRate} suffix=' TOKEN' displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></div>
            <div>My Farm Share: <Number value={farmShare} suffix=' %' displayType='text' thousandSeparator /></div>
            <div>My Total Rewards: <Number value={parseFloat(utils.formatEther(stakerInfo.reward)) + parseFloat(unclaimedRewards)} suffix=' TOKEN' displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /></div>
            <div>My Unclaimed Rewards: <Number value={unclaimedRewards} suffix=' TOKEN' displayType='text' thousandSeparator fixedDecimalScale decimalScale={0} /> <Button compact onClick={claimRewards}>Claim</Button></div>
          </Segment>
        </Segment>


        <Segment>
          <Button primary onClick={stakeAll}>Stake All (Up to 200) (WAIT AFTER CLICK!)</Button>
          <Button primary onClick={unstakeAll}>Unstake All (Up to 200) (WAIT AFTER CLICK!)</Button>
        </Segment>

      </Container>
    </Page>
  );
};

export default hot(module)(NftStakingPage);
